body {
  position: relative;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  color: inherit;
}
img {
  max-width: 100%;
  height: auto;
}
.banner {
  min-width: 370px;
  min-height: 300px;
  width: 100%;
  margin-bottom: 50px;
  background-image: url('../img/banner.jpg');
  background-position: top -300px center;
}
.gallery {
  width: 370px;
  padding-left: 10px;
  padding-right: 10px;
  margin: 0 auto;
}
@media (min-width: 760px) {
  .gallery {
    width: 760px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media (min-width: 1170px) {
  .gallery {
    width: 1170px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
.gallery__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.gallery__item {
  width: 350px;
  margin-bottom: 30px;
}
@media (min-width: 760px) {
  .gallery__item:not(:nth-child(2n)) {
    margin-right: 20px;
  }
}
@media (min-width: 1170px) {
  .gallery__item:not(:nth-child(2n)) {
    margin-right: 0;
  }
  .gallery__item:not(:nth-child(3n)) {
    margin-right: 30px;
  }
}
.gallery__link {
  display: flex;
  justify-content: center;
  width: 350px;
  height: 233px;
}
.gallery__link:hover {
  cursor: pointer;
}
.gallery__image {
  align-self: center;
  max-height: 100%;
}
.gallery__description {
  font-size: 17px;
  color: #000000;
}
.gallery__date {
  font-size: 15px;
  color: #777777;
}
.review {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  padding-top: 150px;
  padding-bottom: 20px;
  background-color: #333333;
  overflow: hidden;
}
.review__container {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 30px;
}
.review__main-container {
  width: 40vw;
}
.review__main-image {
  width: 100%;
}
.review__previous-container {
  position: relative;
  width: 20vw;
}
.review__next-container {
  position: relative;
  width: 20vw;
}
.review__next-image,
.review__previous-image {
  position: absolute;
  max-width: none;
  height: 100%;
  opacity: 0.3;
  transition: 0.3s;
}
.review__next-image:hover,
.review__previous-image:hover {
  cursor: pointer;
  opacity: 0.5;
}
.review__next-image {
  left: 0;
}
.review__previous-image {
  right: 0;
}
.review__description {
  width: 40vw;
  margin: 0 auto;
  padding: 0 20px;
  font-size: 20px;
  line-height: 30px;
  color: #aaaaaa;
}
.review__close {
  position: absolute;
  top: 20px;
  right: 50px;
  width: 40px;
  height: 40px;
  background-color: transparent;
  border: none;
}
.review__close:hover {
  cursor: pointer;
}
.review__close::before,
.review__close::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 2px;
  margin-top: -1px;
  background-color: #ffffff;
}
.review__close::before {
  transform: rotate(45deg);
}
.review__close::after {
  transform: rotate(-45deg);
}
